.book-list {
  height: 200px;
  padding: 25px;
  background: rgb(255, 228, 255);
  display: flex;
  align-items: center;
  justify-content: flex-start;
  margin-bottom: 10px;
}
.book-list img {
  width: 120px;
  height: 150px;
  border: 1px solid #000;
  flex-shrink: 0;
  margin-right: 30px;
}
.book-info {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.book-title > p {
  display: inline-block;
  padding: 3px 5px;
  background: #ccc;
  text-align: center;
  border-radius: 3px;
}
.book-title > span {
  font-size: 13px;
}
.book-title div {
  margin-top: 20px;
}
.book-title div p {
  margin-bottom: 10px;
}
.book-btn button {
  width: 130px;
  height: 40px;
  border: 1px solid #000;
  border-radius: 2px;
}

.plus-top {
  display: flex;
  justify-content: space-between;
}
.plus-tab {
  overflow: hidden;
}
.plus-tab li {
  float: left;
  margin-right: 10px;
}
.plus-tab li a {
  display: block;
  width: 150px;
  height: 40px;
  text-align: center;
  line-height: 40px;
  border: 1px solid #fff;
}
.plus-tab li.active a {
  background: orange;
}
.plus-search {
  display: flex;
  align-items: center;
}
.plus-search select {
  height: 30px;
  width: 120px;
}
.plus-search button {
  width: 40px;
  height: 28px;
  background: #fff;
}
.plus-search > * {
  margin-left: 5px;
}
.plus-bottom {
  margin-top: 10px;
}
.plus-bottom table {
  width: 100%;
  text-align: center;
}
.plus-bottom table th {
  height: 50px;
  line-height: 50px;
  background: yellowgreen;
  border: 1px solid #fff;
}
.plus-bottom table td {
  height: 50px;
  line-height: 50px;
  border: 1px solid #000;
  padding: 0 5px;
}
.plus-bottom table td button {
  width: 120px;
  height: 40px;
  line-height: 38px;
  border: 1px solid #000;
  border-radius: 5px;
}
.plus-bottom table td button.wait {
  background: #e0c3c3;
}
.plus-bottom table td button.study {
  background: #c8e6ca;
}

.text-left {
  text-align: left;
}


